.top {
	display: flex;
	border-radius: 5px 5px 0 0;
	background: #f5f5f5 url(../img/top.jpg) repeat-x;
	padding: 10px 32px 10px;
	align-items: center;
	@include r(1020) {
		border-radius: 0; }
	@include r(520) {
		background-size: cover; } }

.link {
	display: flex;
	text-decoration: none;
	&__btn {
		padding: 1px 4px;
		background-color: #85afbd;
		border-radius: 2px;
		text-transform: uppercase;
		@include font-decor;
		color: white;
		font-size: 14px; }
	&__header {
		color: #757575;
		@include font-main_italic;
		font-size: 11px;
		line-height: 18px;
		padding-left: 11px;
		padding-top: 2px; }
	&:hover &__btn {
		background-color: $color-decoration; }
	&:hover &____header {
		text-decoration: underline; } }

.icon {
	padding-left: 10px; }

.search {
	position: absolute;
	top: center;
	right: 34%;
	@include r(860) {
		right: 20%; }
	@include r(670) {
		display: none; } }

.arrow {
 background: transparent url(../img/arrow.png) no-repeat;
 height: 10px; }


.header {
	background: #1a303d url(../img/main_header.png) no-repeat;
	background-size: cover;
	display: flex;
	@include r(600) {
		flex-wrap: wrap;
		justify-content: center; } }

.logo {
	margin: 28px 39px 20px 30px;
	height: 76px;
	width: 146px;
	@include r(600) {
		margin-bottom: 0; } }

.navigation {
	margin: 25px 0;
	@include font-decor; }

.help-nav {
	display: flex;
	@include r(900) {
		justify-content: center; }
	&__item {
		margin-right: 11px;
		color: #a6a6a6;
		text-transform: uppercase;
		font-size: 14px;
		display: inline-block;
		position: relative;
		&::before {
			display: block;
			position: absolute;
			content: "";
			height: 2px;
			width: 0;
			background-color: #a6a6a6;
			transition: width .5s ease-in-out, left .5s ease-in-out;
			left: 50%;
			bottom: 0; }
		&:hover::before {
			width: 100%;
			left: 0; } } }


.main-nav {
	margin: 14px 0 0 -13px;
	display: flex;
	@include r(900) {
		justify-content: center;
		flex-wrap: wrap; }
	&__item {
		padding: 5px 13px;
		color: #f6f6f6;
		text-transform: uppercase;
		text-decoration: none;
		font-size: 18px;
		@include r(540) {
			padding: 3px 10px; }
		&:hover {
			box-shadow: 1px 1px 0 rgba(255, 255, 255, 0.1), inset 1px 1px 1px rgba(0, 0, 1, 0.28);
			background-color: #092f3b; } } }
