@import url("https://fonts.googleapis.com/css?family=Barlow+Condensed");
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol, li {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

h1, h2, h3, h4, h5, h6 {
  font-weight: 400;
}

a {
  text-decoration: none;
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

.comments {
  padding-left: 20px;
  padding-bottom: 10px;
  background: transparent url(../img/icon_talk.png) no-repeat;
  font-family: Georgia, serif;
  font-style: italic;
  font-size: 11px;
  color: #757575;
}

.top {
  display: flex;
  border-radius: 5px 5px 0 0;
  background: #f5f5f5 url(../img/top.jpg) repeat-x;
  padding: 10px 32px 10px;
  align-items: center;
}

@media only screen and (max-width: 1020px) {
  .top {
    border-radius: 0;
  }
}

@media only screen and (max-width: 520px) {
  .top {
    background-size: cover;
  }
}

.link {
  display: flex;
  text-decoration: none;
}

.link__btn {
  padding: 1px 4px;
  background-color: #85afbd;
  border-radius: 2px;
  text-transform: uppercase;
  font-family: 'Barlow Condensed', sans-serif;
  font-weight: 400;
  color: white;
  font-size: 14px;
}

.link__header {
  color: #757575;
  font-family: Georgia, serif;
  font-style: italic;
  font-size: 11px;
  line-height: 18px;
  padding-left: 11px;
  padding-top: 2px;
}

.link:hover .link__btn {
  background-color: #ae0002;
}

.link:hover .link____header {
  text-decoration: underline;
}

.icon {
  padding-left: 10px;
}

.search {
  position: absolute;
  top: center;
  right: 34%;
}

@media only screen and (max-width: 860px) {
  .search {
    right: 20%;
  }
}

@media only screen and (max-width: 670px) {
  .search {
    display: none;
  }
}

.arrow {
  background: transparent url(../img/arrow.png) no-repeat;
  height: 10px;
}

.header {
  background: #1a303d url(../img/main_header.png) no-repeat;
  background-size: cover;
  display: flex;
}

@media only screen and (max-width: 600px) {
  .header {
    flex-wrap: wrap;
    justify-content: center;
  }
}

.logo {
  margin: 28px 39px 20px 30px;
  height: 76px;
  width: 146px;
}

@media only screen and (max-width: 600px) {
  .logo {
    margin-bottom: 0;
  }
}

.navigation {
  margin: 25px 0;
  font-family: 'Barlow Condensed', sans-serif;
  font-weight: 400;
}

.help-nav {
  display: flex;
}

@media only screen and (max-width: 900px) {
  .help-nav {
    justify-content: center;
  }
}

.help-nav__item {
  margin-right: 11px;
  color: #a6a6a6;
  text-transform: uppercase;
  font-size: 14px;
  display: inline-block;
  position: relative;
}

.help-nav__item::before {
  display: block;
  position: absolute;
  content: "";
  height: 2px;
  width: 0;
  background-color: #a6a6a6;
  transition: width .5s ease-in-out, left .5s ease-in-out;
  left: 50%;
  bottom: 0;
}

.help-nav__item:hover::before {
  width: 100%;
  left: 0;
}

.main-nav {
  margin: 14px 0 0 -13px;
  display: flex;
}

@media only screen and (max-width: 900px) {
  .main-nav {
    justify-content: center;
    flex-wrap: wrap;
  }
}

.main-nav__item {
  padding: 5px 13px;
  color: #f6f6f6;
  text-transform: uppercase;
  text-decoration: none;
  font-size: 18px;
}

@media only screen and (max-width: 540px) {
  .main-nav__item {
    padding: 3px 10px;
  }
}

.main-nav__item:hover {
  box-shadow: 1px 1px 0 rgba(255, 255, 255, 0.1), inset 1px 1px 1px rgba(0, 0, 1, 0.28);
  background-color: #092f3b;
}

.article-heading {
  display: flex;
}

@media only screen and (max-width: 770px) {
  .article-heading {
    flex-wrap: wrap;
  }
}

.article-heading__head {
  font-family: 'Barlow Condensed', sans-serif;
  font-weight: 400;
  background: #ae0002;
  border-radius: 3px 0 0 3px;
  padding: 5px 32px 9px 12px;
  position: relative;
  color: #f6f6f6;
  font-size: 18px;
  line-height: 22px;
  text-transform: uppercase;
}

@media only screen and (max-width: 770px) {
  .article-heading__head {
    height: 36px;
  }
}

@media only screen and (max-width: 450px) {
  .article-heading__head {
    padding-right: 10px;
    font-size: 14px;
  }
}

.article-heading__head::after {
  content: '';
  position: absolute;
  right: -37px;
  top: 0;
  border: 18px solid transparent;
  border-left: 20px solid #ae0002;
}

@media only screen and (max-width: 450px) {
  .article-heading__head::after {
    display: none;
  }
}

.article-heading__text {
  font-family: Georgia, serif;
  font-style: normal;
  color: #757575;
  background-color: #cacaca;
  flex: 1 1 auto;
  padding: 8px 0 11px 37px;
  border-radius: 0 3px 3px 0;
  letter-spacing: 0px;
  font-size: 12px;
  line-height: 16px;
}

@media only screen and (max-width: 770px) {
  .article-heading__text {
    padding-right: 10px;
    font-size: 11px;
  }
}

@media only screen and (max-width: 660px) {
  .article-heading__text {
    padding-left: 10px;
  }
}

.slider {
  margin-top: 4px;
  height: 256px;
  background: #eec38f url(../img/slide.jpg) no-repeat 65% 13%;
  border-radius: 3px;
  position: relative;
}

@media only screen and (max-width: 1020px) {
  .slider {
    background-size: cover;
  }
}

.slider__text {
  position: absolute;
  top: 42%;
  left: 5%;
  width: 47%;
  background-color: #1e1e1e;
  padding: 19px 20px;
  color: white;
  font-family: Georgia;
  font-weight: 400;
  line-height: 18px;
  letter-spacing: 0.1px;
}

@media only screen and (max-width: 540px) {
  .slider__text {
    top: 20%;
    left: 20%;
    width: 60%;
  }
}

.main-articles {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  margin-top: 15px;
  margin-bottom: 45px;
}

@media only screen and (max-width: 763px) {
  .main-articles {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media only screen and (max-width: 400px) {
  .main-articles {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

.article-link {
  border-radius: 5px;
  padding: 16px;
  padding-bottom: 7px;
  background-color: white;
  margin-right: 16px;
  position: relative;
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.2);
}

@media only screen and (max-width: 1020px) {
  .article-link {
    width: 170px;
    margin: 0 auto;
  }
}

@media only screen and (max-width: 763px) {
  .article-link {
    width: 230px;
    margin-top: 20px;
  }
}

@media only screen and (max-width: 540px) {
  .article-link {
    width: 220px;
  }
}

.article-link:hover {
  transform: scale(1.5, 1.5);
  z-index: 1;
}

@media only screen and (max-width: 1020px) {
  .article-link:hover {
    transform: scale(1, 1);
  }
}

.article-link:last-child {
  margin-right: 0;
}

@media only screen and (max-width: 1020px) {
  .article-link:last-child {
    margin: 0 auto;
  }
}

@media only screen and (max-width: 763px) {
  .article-link:last-child {
    margin-top: 20px;
  }
}

.main-article__img {
  width: 137px;
  height: 117px;
}

@media only screen and (max-width: 1020px) {
  .main-article__img {
    width: 100%;
  }
}

.main-article__head {
  margin: 10px 0 7px 2px;
  color: #042b38;
  font-family: Georgia, serif;
  font-style: normal;
  font-size: 12px;
  line-height: 1.5;
  text-decoration: underline;
}

.main-article__text {
  margin: 0 0 10px 2px;
  color: #757575;
  font-family: Georgia, serif;
  font-style: italic;
  font-size: 11px;
  line-height: 18px;
  padding-right: 15px;
}

.main-article__comments {
  position: absolute;
  bottom: 0;
  right: 14px;
}

.big-article {
  display: grid;
  grid-template-columns: 1fr 3fr;
  margin: 24px 0 20px;
}

@media only screen and (max-width: 513px) {
  .big-article {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

.big-article__img {
  width: 168px;
  height: 130px;
}

@media only screen and (max-width: 513px) {
  .big-article__img {
    margin-bottom: 20px;
  }
}

.big-article__info {
  font-family: Georgia, serif;
  font-style: normal;
  color: #757575;
  line-height: 18px;
  margin: 9px 0 13px 15px;
}

.socials {
  margin-top: 19px;
  display: flex;
}

.tags {
  margin-left: 26px;
}

.tags__link {
  text-shadow: 1px 1px 0 rgba(255, 255, 255, 0.3);
  color: #2b5e5e;
  font-style: italic;
  text-decoration: underline;
}

.tags__link:first-child {
  padding-left: 7px;
}

.share {
  display: flex;
  margin-left: 32px;
}

.share__icon {
  margin-left: 11px;
  padding-left: 20px;
}

.share__icon:hover {
  transform: scale(1.5, 1.5);
}

@media only screen and (max-width: 540px) {
  .share__icon:hover {
    transform: scale(1, 1);
  }
}

.facebook {
  background: transparent url(../img/facebook.png) no-repeat;
}

.twitter {
  background: transparent url(../img/twit.png) no-repeat;
  margin-left: 0;
}

.out {
  background: #c8deeb url(../img/bg_image.jpg) no-repeat;
  background-size: cover;
  font-size: 12px;
  padding-bottom: 100px;
}

@media only screen and (max-width: 1020px) {
  .out {
    padding-bottom: 50px;
  }
}

.content {
  max-width: 1000px;
  margin: 0 auto;
  padding-top: 34px;
}

@media only screen and (max-width: 1020px) {
  .content {
    padding-top: 0;
  }
}

.articles {
  background-color: #ededed;
  display: flex;
  padding: 30px 15px 20px 32px;
}

@media only screen and (max-width: 1020px) {
  .articles {
    display: block;
    padding-left: 10px;
    padding-right: 10px;
  }
}

.main {
  width: 76%;
}

@media only screen and (max-width: 1020px) {
  .main {
    width: 100%;
  }
}

.aside {
  width: 24%;
  padding: 0 15px 0 26px;
}

@media only screen and (max-width: 1020px) {
  .aside {
    width: 100%;
    padding-left: 0;
    padding-right: 0;
  }
}

.footer {
  margin: 280px 2px 150px;
}

@media only screen and (max-width: 1020px) {
  .footer {
    margin: 50px 0;
    text-align: center;
  }
}

.aside-heading {
  background-color: #ae0002;
  text-transform: uppercase;
  padding: 4px 12px 8px;
  border-radius: 3px;
  font-family: 'Barlow Condensed', sans-serif;
  font-weight: 400;
  letter-spacing: 0.15px;
}

.aside-heading__link {
  color: #f6f6f6;
  display: block;
  transition: all 3s;
}

.aside-heading__link:hover {
  text-decoration: underline;
}

@media only screen and (max-width: 1020px) {
  .aside-articles {
    display: flex;
    justify-content: space-around;
  }
}

@media only screen and (max-width: 600px) {
  .aside-articles {
    flex-direction: column;
    align-items: center;
  }
}

.aside-article {
  margin: 22px 0;
}

@media only screen and (max-width: 1020px) {
  .aside-article {
    max-width: 186px;
    margin-left: 20px;
  }
}

@media only screen and (max-width: 600px) {
  .aside-article {
    margin-left: 0;
  }
}

.aside-article:first-of-type {
  margin-top: 7px;
}

@media only screen and (max-width: 1020px) {
  .aside-article:first-of-type {
    margin-top: 23px;
    margin-left: 0;
  }
}

.aside-article__heading {
  padding-top: 12px;
  display: block;
  color: #042b38;
  font-family: Georgia, serif;
  font-style: normal;
  font-size: 12px;
  text-decoration: underline;
  padding-left: 3px;
  letter-spacing: 0;
  line-height: 1.5;
  margin-top: 3px;
  margin-bottom: 8px;
}

.aside-article__text {
  margin-top: 6px;
  margin-bottom: 20px;
  color: #757575;
  font-family: Georgia, serif;
  font-style: italic;
  font-size: 11px;
  line-height: 18px;
  padding-left: 3px;
  display: inline;
}

.aside-article__comments {
  margin-left: 12px;
}

.preview {
  width: 186px;
  height: 172px;
  display: block;
  position: relative;
}

.preview__img {
  z-index: 10;
}

.preview__click {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  display: flex;
  align-items: center;
  text-align: center;
  color: white;
  font-size: 25px;
  padding: 0 15px;
  background-color: rgba(27, 49, 62, 0.5);
  z-index: -1;
}

.preview:hover .preview__click {
  z-index: 1;
}
