.aside-heading {
	background-color: $color-decoration;
	text-transform: uppercase;
	padding: 4px 12px 8px;
	border-radius: 3px;
	@include font-decor;
	letter-spacing: 0.15px;
	&__link {
		color: #f6f6f6;
		display: block;
		transition: all 3s;
		&:hover {
			text-decoration: underline; } } }

.aside-articles {
	@include r(1020) {
		display: flex;
		justify-content: space-around; }
	@include r(600) {
		flex-direction: column;
		align-items: center; } }
.aside-article {
	margin: 22px 0;
	@include r(1020) {
		max-width: 186px;
		margin-left: 20px; }
	@include r(600) {
		margin-left: 0; }
	&:first-of-type {
		margin-top: 7px;
		@include r(1020) {
			margin-top: 23px;
			margin-left: 0; } }
	&__heading {
		padding-top: 12px;
		display: block;
		color: #042b38;
		@include font-main_normal;
		font-size: 12px;
		text-decoration: underline;
		padding-left: 3px;
		letter-spacing: 0;
		line-height: 1.5;
		margin-top: 3px;
		margin-bottom: 8px; }
	&__text {
		margin-top: 6px;
		margin-bottom: 20px;
		color: #757575;
		@include font-main_italic;
		font-size: 11px;
		line-height: 18px;
		padding-left: 3px;
		display: inline; }
	&__comments {
		margin-left: 12px; } }

.preview {
	width: 186px;
	height: 172px;
	display: block;
	position: relative;
	&__img {
		z-index: 10; }
	&__click {
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		display: flex;
		align-items: center;
		text-align: center;
		color: white;
		font-size: 25px;
		padding: 0 15px;
		background-color: rgba(27,49,62,.5);
		z-index: -1; }
	&:hover &__click {
		z-index: 1; } }
