.out {
	background:  #c8deeb url(../img/bg_image.jpg) no-repeat;
	background-size: cover;
	font-size: 12px;
	padding-bottom: 100px;
	@include r(1020) {
		padding-bottom: 50px; } }

.content {
	max-width: 1000px;
	margin: 0 auto;
	padding-top: 34px;
	@include r(1020) {
		padding-top: 0; } }

.articles {
	background-color: #ededed;
	display: flex;
	padding: 30px 15px 20px 32px;
	@include r(1020) {
		display: block;
		padding-left: 10px;
		padding-right: 10px; } }
.main {
	width: 76%;
	@include r(1020) {
		width: 100%; } }

.aside {
	width: 24%;
	padding: 0 15px 0 26px;
	@include r(1020) {
		width: 100%;
		padding-left: 0;
		padding-right: 0; } }

.footer {
	margin: 280px 2px 150px;
	@include r(1020) {
		margin: 50px 0;
		text-align: center; } }
